.answers-block{
  height: 163vh;
  background-image: url("./assets/bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  object-position: 65px -15px;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  .answers-block-main::before{
    content: "";
    content: "";
    background: indigo;
    z-index: -1;
    border-radius: 15px;
    border: 1px solid rgba(43, 43, 43, 0.568);
    -webkit-backdrop-filter: blur(10px);
    /* backdrop-filter: blur(453px
px
); */
    position: absolute;
    height: 100%;
    filter: blur(233px);
    width: 20%;
    border-radius: 15px;
    border: 1px solid rgba(43, 43, 43, 0.568);
  }
    .spoiler {
        color: white;
        font-family: "Montserrat", system-ui;
        user-select: none;
        justify-content: space-around;
        font-size: 20px;
        margin-top: 10px;
        
        label {
        
        }
    }
    .spoiler-line {
        width: 99%;
        // padding-top: 15px;
        border-bottom: 1px solid #999999;
    }

    .spoiler input, .spoiler p  { 

        font-size: 15px;
        color: antiquewhite;
        font-weight: lighter;
        transition: 1s;
        display: none; 
        user-select: none;
        margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
      }

      .spoiler :checked ~ p {
        transition: 1s;
        display: block;
      }

      .spoiler-arrow{
        // padding-left: 200px;
        // margin-bottom: 10px;

        display: flex;
        justify-content: flex-end;
        // margin-bottom: 10px;
        position: relative;
        top: -29px;
      }

      /*   */
.spoiler .spoiler-arrow::before {
    user-select: none;
    content: url("./assets/up.svg"); 
    margin-right: 10px;
    user-select: none;
  }
  /*   */
  .spoiler :checked + label::before {
    content: url("./assets/down.svg");
    user-select: none;
    
  }
    .answers-block-header{
        padding-top: 50px;
        width: 550px;
        text-align: center;
        /* display: grid; */
      margin: 0 auto;
        h1{
            padding-top: 30px;
        }
        h2{
            padding-top: 125px;
        }
        .answers-block-header-side-info{
            
            color: white;
            font-size: 20px;
            font-family: "Montserrat", system-ui;
        }
    }
    .answers-block-main::-webkit-scrollbar {
        width: 0;
      }
    .answers-block-main {
        // width: 100%;
